import { lazy, Suspense, useRef, useEffect } from "react";
import { BrowserRouter as Router, Route, Routes, Navigate, HashRouter } from "react-router-dom";
import AppHeader from "../appHeader/AppHeader";
import Spinner from "../spinner/Spinner";

// import Page404 from "../pages/404";
const Page404 = lazy(() => import('../pages/404'))
const MainPage = lazy(() => import('../pages/MainPage'))
const ComicsPage = lazy(() => import('../pages/ComicsPage'))
const SingleComicPage = lazy(() => import('../pages/SingleComicPage'))
const SinglePage = lazy(() => import('../pages/SinglePage'))

const App = () => {

    useEffect(() => {
        const handleBeforeUnload = (event) => {
            event.preventDefault();
            localStorage.clear();
            return undefined
        };
        window.addEventListener('beforeunload', handleBeforeUnload);
        return () => {
          window.removeEventListener('beforeunload', handleBeforeUnload);
        };
      }, []);
    return (
        <HashRouter>
            <div className="app">
                <AppHeader/>
                <main>
                    <Suspense fallback={<Spinner/>}>
                        <Routes>
                            <Route path='/' element={<MainPage/>}/>
                            <Route path='/characters/:charId' element={<SinglePage item='char' id='charId'/>}/>
                            <Route path='/comics' element={<ComicsPage/>}/>
                            <Route path='/comics/:comicId' element={<SinglePage item='comic' id='comicId'/>}/>
                            <Route path='*' element={<Page404/>}/>
                        </Routes>
                    </Suspense>
                </main>
            </div>
        </HashRouter >
    )
}


export default App;